.home .headline {
  position: absolute;
  right: 10%;
  top: 14%;
  overflow: hidden;
  z-index: 3;
}
.home .headline::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: #f07d00;
  animation: a-ltr-after 2s cubic-bezier(0.77, 0, 0.18, 1) forwards;
  transform: translateX(-101%);
}
.home .headline::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  animation: a-ltr-before 2s cubic-bezier(0.77, 0, 0.18, 1) forwards;
  transform: translateX(0);
}
.home .headline:nth-of-type(1)::before, .home .headline:nth-of-type(1)::after {
  animation-delay: 1s;
}
.home .headline:nth-of-type(2)::before, .home .headline:nth-of-type(2)::after {
  animation-delay: 1s;
}
.home .headline h1,
.home .headline h2 {
