@media screen and (max-width: 991px) {
  #tools .carousel-item img {
    width: 250px;
    height: 125px;
  }
}
.video {
  position: relative;
  width: 100%;
  max-width: 450px;
  text-align: center;
}
.video a {
  display: inline-block;
  width: 100%;
  background-color: #000;
}
.video a::after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-image: url(./../../assets/icons/play-button.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.video a:active::after, .video a:focus::after, .video a:hover::after {
  background-image: url(./../../assets/icons/play-button-hover.svg);
}
.video img {
  width: 100%;
  height: 100%;
}

@media screen and (min-width: 992px) {
  .video {
    margin-left: 50px;
    float: right;
  }
  .video a::after {
    width: 100px;
    height: 100px;
  }
}
@media screen and (max-width: 991px) {
  .video {
    margin: 0 auto 30px auto;
  }
  .video a::after {
    width: 70px;
    height: 70px;
  }
}